export const authEndpoint = "https://accounts.spotify.com/authorize";
const clientId = "8e0f9e1b4f734440aa98873c67d87250";
const redirectUri = "https://vishwavrat2802.github.io/spotify-clone";
const scopes = [
    "user-read-currently-playing",
    "user-read-recently-played",
    "user-read-playback-state",
    "user-top-read",
    "user-modify-playback-state"
]
export const getTokenFromUrl = () => {
    return window.location.hash
            .substring(1)
            .split("&")
            .reduce((initial,item) => {
                var parts = item.split("=");
                initial[parts[0]] = decodeURIComponent(parts[1]);
                return initial;
            } , {});
}
export const loginUrl = `${authEndpoint}?response_type=token&client_id=${clientId}&scope=${scopes.join("%20")}&redirect_uri=${redirectUri}&show_dialog=true`;
getTokenFromUrl();